@import "./colors", "./sidebar", "./nav", "./hero", "./shared", "./about", "./skills", "./projects", "./contact", "./footer";

body {
    background: $main-dark-blue;
    font-size: 1rem;
    scroll-behavior: smooth;
}
* {
    color: $main-light-blue;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', serif;
    
    // change to sans-serif before deployment
}
a, li{
    text-decoration: none;
    color: inherit;
    list-style: none;
}